<style scoped>

  .wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    padding-top: 150px;
    padding-bottom: 200px;
  }

  .wrapper > h1 {
    text-align: center;
    vertical-align: middle;
    margin-bottom: 20px;
    color: #FFF;
  }

  .login {
    margin: 0 auto;
    padding: 200px auto;
    width: 200px;
    height: 100%;
  }

</style>

<template>

  <div class="wrapper">
    <h1>
      Management Platform
    </h1>
    <div class="login">
      <i-form ref="formInline" :model="formInline" :rules="ruleInline">
        <Form-item prop="username">
          <Input v-model="formInline.username"></Input>
        </Form-item>
        <Form-item prop="password">
          <Input v-model="formInline.password" type="password"></Input>
        </Form-item>
        <Form-item>
          <i-button type="success" @click.native="handleSubmit('formInline')" long>登录</i-button>
        </Form-item>
      </i-form>
    </div>
    <div id="indexLizi" class=""><div style="position: relative; top: 200px;"><canvas width="1920" height="973"></canvas></div></div>
  </div>

</template>

<script>
  import axios from "axios"
  export default {
    data() {
      return {
        loginUrl: $fn.apiHost+"login",
        formInline: {
          username: '',
          password: '',
        },
        ruleInline: {
          user: [{
            required: true,
            message: '请填写用户名',
            trigger: 'blur'
          }],
          password: [{
            required: true,
            message: '请填写密码',
            trigger: 'blur'
          }, {
            type: 'string',
            min: 5,
            message: '密码长度不能小于5位',
            trigger: 'blur'
          }]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        let _self = this
        this.$refs[name].validate((valid) => {
          if (valid) {
            axios.post(this.loginUrl, this.formInline, {
              withCredentials: true,
              headers: {
                'x-requested-with': 'XMLHttpRequest'
              },
              responseType: 'json'
            })
              .then(function (response) {
                $fn.setJwtToken(response.data.token)
                console.log(response.data.token)
                console.log('Seession Token'+$fn.getJwtToken())
                _self.$router.push('/about');
              })
              .catch(function (error) {
                console.log(error);
              });
            this.$Message.success('提交成功!');


          } else {
            this.$Message.error('表单验证失败!');
          }
        })
      },
      handleReset(val) {
        console.log(val)
      }
    }
  }

</script>



